/* RSS
-------------------------------------------------- */
.read-true {
  .event-date, .rss-title, .rss-description {
    color: lighten($app-heading-color, 40%);
  }
}

.read-false {
  .rss-title {
    font-weight: bold;
  }
  //border: solid 4px $app-border-color;
  // background-color: $app-border-color;
}

.report-entry {
  &.rss-entry {
    .report-col {
      padding: $spacing;
      vertical-align: top;
       @include mediaQueries(max-small) {
        border: 0;
        display: block;
    }
    }
  }
}
