/* Login
-------------------------------------------------- */
#page_login, #page_password,#page_passwordforgot,#page_passwordreset{ 
    #header,#footer{
        display: none !important; 
    }
    .container {
        max-width: 500px;
    }
    .login-lang{
        text-align: right;
        margin-bottom: $spacing;
    }


}

.group-login-lang{
    // Media queries
    @include mediaQueries(max-esmall) {
        display: block;
    }
}
/* Images
-------------------------------------------------- */
.lang-img{
    max-width: 32px;
    max-height: 32px;
}