/* Elements
-------------------------------------------------- */
.widget-entry-element {
    .widget-entry-in {
        height: 116px;
        // Media queries
        @include mediaQueries(min-small) {
            height: 124px;
        }
        .widget-img {
            width: 64px;
            height: 64px;     
            background-color: #fff;
            @include appBorderRadius(50%);

            &.mobile {
                display: block;
                @include mediaQueries(min-small) {
                    display: none;            
                }
            }

            display: none;
                @include mediaQueries(min-small) {
                    display: block;            
                } 

            .widget-icon-update {

                //z-index: 10;
                cursor: pointer;
                //border-color: $element-widget-img-update-border-color;
            }
            // Media queries
            @include mediaQueries(min-small) {
                width: 64px;
                height: 64px;  
            }

            .widget-img-overlay {
                background-color: rgba(255,255,255, 0.8);
                width: 64px;
                height: 64px;
                display: table;
                @include appBorderRadius(50%);
                position: absolute;
                // Media queries
                @include mediaQueries(min-small) {
                    display: none;
                }
                .widget-level {
                    display: table-cell;
                    vertical-align: middle;
                    font-size: 120%;
                    text-align: center;
                    font-weight: bold;
                }

                .widget-scale {
                    display: table-cell;
                    vertical-align: middle;
                    font-size: 80%;
                    text-align: left;
                }
            }
        }

        .widget-content{
            // Media queries
            @include mediaQueries(max-esmall) {
                margin-left:0;
            }
        }
        .widget-title {
            text-align: center;
            // Media queries
            @include mediaQueries(min-small) {
                text-align: left;
            }
            @include mediaQueriesMaxMin($site-max-w,$break-large) {
                max-width: 200px;
                @include textOverflow();
            }
            h3{
                font-size: 75%;
                @include textOverflow();
                @include mediaQueries(min-small) {
                    font-size: 95%;
                }
            }
            .widget-room {
                display: block;
                @include textOverflow();
                font-size: 75%;;
                color: lighten($app-body-color, 40%);
                // Media queries
                @include mediaQueries(min-small) {
                    font-size: 85%;
                }
                .widget-title {
                    // Media queries
                    @include mediaQueriesMaxMin($site-max-w,$break-large) {
                        max-width: 200px;
                        @include textOverflow();
                    }
                    // Media queries
                    @include mediaQueries(max-small) {
                        h3{
                            font-size: 95%;
                            padding: 0 0 0 64px + $spacing;
                        }
                    }
                    .widget-room {
                        white-space: nowrap;
                        display: block;
                        font-size: 85%;
                        color: lighten($app-body-color, 40%);
                        // Media queries
                        @include mediaQueries(max-esmall) {
                            padding: 0 0 0 64px + $spacing;
                        }
                    }
                }
                .widget-level {
                    font-weight: bold;
                    font-size: 130%;
                    color: $app-body-color;
                }
                .widget-scale {
                    font-size: 110%;
                }

            }
        }
        &.isfailed-true{
            .widget-entry-in {
                opacity: 0.8;
                border-color: $app-bcg-danger;
                .widget-preview-img{
                    opacity: 0.6;
                }
            }
        }
    }
}

.widget-ctrl, .tbl-widget-ctrl {
    .fa-caret-down {
        font-size: 140%;
        color: lighten($app-body-color, 40%);
    }
    .btn-group {
        .btn {
            color: lighten($app-body-color, 80%);
            .fa {
                font-size: 110%;
            }
        }
        .widget-btn-symbol {
            font-weight: bold;
            display: inline-block;
            padding: 0 5px;
        }
        .btn-primary {
            background-color: #FFFFFF !important;
            border-color: lighten($app-body-color, 80%);
            color: $app-brand-success !important;
            @include appBoxShadow($element-widget-btn-box-shadow);
            .fa {
                color: $app-brand-success !important;
            }
        }
        .btn-off.btn-primary {
            color: $app-brand-danger !important;
            .fa {
                color: $app-brand-success !important;
            }
        }
        .btn-label-on {
            color: lighten($app-body-color, 40%);
            text-transform: lowercase;
        }
        .btn-widget-time, .txt-widget-time {
            display: none;
            @include mediaQueries(min-small) {

                padding: $spacing 0 0 0;
                border: 0 none;
                color: lighten($app-body-color, 40%);
                font-size: 85%;
                background-color: transparent;
            }

        }
    }
}

/* Specifics element styles
----------------------------------------------------*/
