/* Elements drag & drop cards
----------------------------------------------------*/
.dragdrop-element{
    .dd-placeholder{
        width: 100%;//$widget-col-w;

        text-align: center;
        //background: whitesmoke;
        border: 3px dashed $app-brand-success;
        box-sizing: border-box;
        display: table;
        background-color: #FFFFFF;
        &.dd-element{
            width: 50%;
            @include appFloat(left);
            height:  130px;
            // Media queries
            @include mediaQueries(min-usmall) {
                width: 33.33333%;
            }
            /*@include mediaQueriesMin(600px){
                width: 20%;
            }*/
            @include mediaQueries(min-small) {
                width: 50%;
            }
            @include mediaQueries(min-large) {
                width: 33.33333%;
            }
        }
    }
}
.card-element-dragdrop {
    @include appGradient();
    cursor: move;
    text-align: center;
    border: solid 1px $app-border-color;
    width: 50%;
    height: 130px;
    @include appFloat(left);
    @include mediaQueries(min-usmall) {
        width: 33.33333%;
    }
    /*@include mediaQueries(min-esmall) {
        width: 25%;
    }
    @include mediaQueriesMin(600px){
         width: 20%;
    }*/
    @include mediaQueries(min-small) {
       width:50%;
    }
    @include mediaQueries(min-large) {
        width: 33.33333%;
    }
    /* Inner element */
    .card-element-dragdrop-in {
        padding: $spacing;
        word-break: break-word;
        .element-dragdrop-img{
            max-width: 48px;
            max-height: 48px;
            //border: solid 4px $widget-img-border-color;
            @include appBorderRadius(50%);
        }
        h3{
            margin: $spacing 0 0 0;
            font-size: 95%;
        }

    }

}

/* Element detail
----------------------------------------------------*/
.btn-tag {
    float: none !important;
    border-radius: 4px !important;
    label {
        padding: 12px 5px 5px;
        @include mediaQueries(min-small) {
            padding: 6px 5px 5px;
        }
        input[type="checkbox"] {
            display: inline-block;
        }    
    }
}

.app-row-icons {
    .report-body {
        width: 30px;
    }
}

/* Custom icons
-------------------------------------------------- */
.element-icon-list {
    border-bottom: solid 1px $app-border-color;
    margin: $spacing;
    .element-icon-img {
        display: inline-block;
        margin: $spacing;
        max-width: 48px;
        max-height: 48px;
        // Media queries
        @include mediaQueries(min-small) {
            max-width: 32px;
            max-height: 32px;
        }
    }
}

/* OpenWeather widget
----------------------------------------------------*/
.widget-body .hidden-sensor-multiline table td {
    text-align: left;
    padding: 5px 0;
}

.widget-body .hidden-sensor-multiline table td:first-child {
    padding-left: 20px;
    vertical-align: top;
}

.widget-body .hidden-sensor-multiline h4 {
    display: inline;
}

.widget-body .hidden-sensor-multiline .text-center {
    margin: 10px 0;
}

/* Text widget
-----------------------------------------------------*/
.widget-body.text-widget h3 {
    font-weight: bold;
}

.widget-body.text-widget-no-img {
    padding-left: 15px;
}

.widget-control.text {
    width: 95%;
}

.text-widget-modal.modal-dialog {
    text-align: center;
}

img.widget-promo-image {
    max-height: 200px;
    margin-bottom: 10px;
}

.widget-promo-text {
    text-align: left;
}

.widget-control.text a {
    font-weight: normal;
}

.widget-control.text a:hover {
    text-decoration: none;
}

.widget-control.text a:after {
    content: "\f1ea";
    font-family: FontAwesome;
    font-style: normal;
    font-weight: normal;
    /*--adjust as necessary--*/
    padding-left: 4px;
    position: absolute;
}

/* MultilevelSensor widget
-----------------------------------------------------*/
.widget-body .element-hidden.hidden-sensor-multiline.multiline-sensor {
    padding: 10px 0;
    height: auto;
}

.hidden-sensor-multiline .widget-image {
    position: relative;
    top: 0;
    left: 0;
    width: 25px;
    height: 25px;
}

.widget-body .hidden-sensor-multiline.multiline-sensor table td:first-child {
    padding-left: 5px;
}

.widget-body .hidden-sensor-multiline.multiline-sensor table td {
    width: inherit;
    padding: 5px 5px 5px 10px;
}

.widget-body .hidden-sensor-multiline.multiline-sensor table tr:nth-child(odd) {
    background-color: #f9f9f9;
}

/* ToggleButton widget
--------------------------------------------------------*/

.toggleButton .widget-body div.widget-control.control-right.ng-scope > div > button.btn.btn-default.btn-sm {
    cursor: default;
}

/* RGB color wheel
--------------------------------------------------------*/
.wheel-colorpicker {
    .col-rgb {
        margin: 0 auto;
        text-align: center;
    }
    #wheel_picker {
        margin: 0 auto;
        text-align: center;
        cursor: pointer;
    }

    #wheel_picker_selected {
        margin: 0 auto;
        text-align: center;
    }

    #wheel_picker_preview { 
        margin: 0 auto;
        text-align: center;
    }

    .wheel-picker-circle-half {
        margin: 0;
        height: 80px;
        line-height: 80px;
    }

    .wheel-picker-circle-half {
        width: 150px;
        height: 75px;
    }

    .wheel-picker-circle-top {
        border-top-left-radius: 150px;
        border-top-right-radius: 150px;
    }

    .wheel-picker-circle-bottom {
        border-bottom-left-radius: 150px;
        border-bottom-right-radius: 150px;
    }
}